/**
 * Content Loader
 * Dynamically loads and renders content sections into the article
 */

export class ContentLoader {
  constructor() {
    this.contentSections = ["introduction", "related-work", "toy-examples"];
    this.articleElement = null;
  }

  async loadContent() {
    this.articleElement = document.querySelector("d-article");
    if (!this.articleElement) {
      console.error("d-article element not found");
      return;
    }

    // Clear existing content except d-contents
    const contentsElement = this.articleElement.querySelector("d-contents");
    this.articleElement.innerHTML = "";
    if (contentsElement) {
      this.articleElement.appendChild(contentsElement);
    }

    // Load each content section
    for (const section of this.contentSections) {
      try {
        await this.loadSection(section);
      } catch (error) {
        console.error(`Failed to load section ${section}:`, error);
      }
    }

    // Add bibliography at the end
    this.addBibliography();

    // Initialize Distill citation system after content is loaded
    // Use a small delay to ensure DOM is fully updated
    setTimeout(() => {
      this.initializeDistillCitations();
    }, 200);
  }

  async loadSection(sectionName) {
    try {
      const response = await fetch(`content/${sectionName}.html`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const html = await response.text();

      // Create a temporary container to parse the HTML
      const tempDiv = document.createElement("div");
      tempDiv.innerHTML = html;

      // Append all child nodes to the article
      while (tempDiv.firstChild) {
        this.articleElement.appendChild(tempDiv.firstChild);
      }
    } catch (error) {
      console.error(`Error loading section ${sectionName}:`, error);
      // Add a placeholder if loading fails
      const errorDiv = document.createElement("div");
      errorDiv.innerHTML = `<p>Error loading section: ${sectionName}</p>`;
      this.articleElement.appendChild(errorDiv);
    }
  }

  addBibliography() {
    // Create the appendix section with bibliography
    const appendix = document.createElement("d-appendix");

    // Add bibliography
    const bibliography = document.createElement("d-bibliography");
    bibliography.setAttribute("src", "bibliography.bib");
    appendix.appendChild(bibliography);

    // Add citation styles
    const style = document.createElement("style");
    style.textContent = `
      d-appendix .citation {
        font-size: 11px;
        line-height: 15px;
        border-left: 1px solid rgba(0, 0, 0, 0.1);
        padding-left: 18px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        background: rgba(0, 0, 0, 0.02);
        padding: 10px 18px;
        border-radius: 3px;
        color: rgba(150, 150, 150, 1);
        overflow: hidden;
        margin-top: -12px;
        white-space: pre-wrap;
        word-wrap: break-word;
      }
    `;
    appendix.appendChild(style);

    // Add citation section
    const citationSection = document.createElement("h3");
    citationSection.id = "citation";
    citationSection.textContent = "Citation";
    appendix.appendChild(citationSection);

    const citationText = document.createElement("p");
    citationText.textContent =
      "For attribution in academic contexts, please cite this work as";
    appendix.appendChild(citationText);

    const shortCitation = document.createElement("pre");
    shortCitation.className = "citation short";
    shortCitation.textContent =
      '"Why do curves matter for LLM interpretability?", 2025.';
    appendix.appendChild(shortCitation);

    const bibtexText = document.createElement("p");
    bibtexText.textContent = "BibTeX citation";
    appendix.appendChild(bibtexText);

    const longCitation = document.createElement("pre");
    longCitation.className = "citation long";
    longCitation.textContent = `@misc{curves_llm_interpretability,
  title={Why do curves matter for LLM interpretability?},
  author={Shivam Raval},
  year={2025},
  url={https://shivam-raval96.github.io/}
}`;
    appendix.appendChild(longCitation);

    // Append the appendix after the article
    this.articleElement.parentNode.insertBefore(
      appendix,
      this.articleElement.nextSibling
    );
  }

  initializeDistillCitations() {
    console.log('Initializing Distill citations...');
    
    // Trigger Distill citation processing
    // Distill processes citations when the DOM changes
    const event = new CustomEvent('distill:update');
    document.dispatchEvent(event);

    // Also try to trigger citation processing directly
    if (typeof window.Distill !== 'undefined') {
      console.log('Distill object found, initializing...');
      if (window.Distill.init) {
        window.Distill.init();
      } else if (window.Distill.citations) {
        window.Distill.citations.init();
      }
    } else {
      console.log('Distill object not found, waiting...');
      // Wait a bit more for Distill to load
      setTimeout(() => {
        if (typeof window.Distill !== 'undefined') {
          console.log('Distill loaded, initializing...');
          if (window.Distill.init) {
            window.Distill.init();
          }
        }
      }, 500);
    }

    // Force a re-render of citations by dispatching a resize event
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
      // Also try to manually process citations if they still don't appear
      this.manualCitationProcessing();
    }, 100);
  }

  manualCitationProcessing() {
    // If Distill citations still don't work, try manual processing
    const citations = document.querySelectorAll('d-cite');
    console.log(`Found ${citations.length} citations to process`);
    
    if (citations.length > 0) {
      // Add a simple citation counter as fallback
      let citationCounter = 1;
      citations.forEach((cite, index) => {
        const bibtexKey = cite.getAttribute('bibtex-key');
        if (bibtexKey) {
          // Create a simple numbered citation
          const citationNumber = document.createElement('sup');
          citationNumber.textContent = `[${citationCounter}]`;
          citationNumber.style.color = '#007BFF';
          citationNumber.style.cursor = 'pointer';
          citationNumber.title = `Citation: ${bibtexKey}`;
          
          // Insert the citation number before the closing tag
          cite.appendChild(citationNumber);
          citationCounter++;
        }
      });
    }
  }
}

// Initialize content loader when DOM is ready
export function initializeContentLoader() {
  const loader = new ContentLoader();
  loader.loadContent();
}
